import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom'
import { useCreateOrderMutation } from '../services/appApi';
import { Form, Col, Row, Alert, Button } from 'react-bootstrap';
import { CardElement,useStripe, useElements } from '@stripe/react-stripe-js'
// import ModalPopup from './ModalPopup';

function CheckoutForm() {
    const stripe = useStripe();
    const elements = useElements();
    const [openModal, setopenModal] = useState(true)
    const navigate = useNavigate();
    const [country, setCountry] = useState("");
    const [address, setAddress] = useState("");
    const [paying, setPaying] = useState(false);
    const user = useSelector((state) => state.user);
    const [alertMessage, setAlertMessage] = useState("");
    const [createOrder, {isLoading, isError, isSuccess}] = useCreateOrderMutation();


    async function handlePay(e) {
        e.preventDefault();
        if(!stripe || !elements || user.cart.count <= 0 ) return;
        setPaying(true);
        const {client_secret} = await fetch("http://localhost:3005/create-payment", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ amount: user.cart.total })
        }).then((res) => res.json());
        const {paymentIntent} = await stripe.confirmCardPayment(client_secret, {
            payment_method: {
                card: elements.getElement(CardElement),
            },
        });
        setPaying(false);

        if(paymentIntent){
            createOrder({userId: user._id, cart: user.cart, address:address,country: country}).then((res) => {
                if(!isLoading && !isError) {
                    setAlertMessage(`Payment ${paymentIntent.status}`);
                    setTimeout(() => {
                        navigate('/orders')
                    }, 50000);
                }
            })
        }
    }

  return (
    <Col className="cart-payment-container">
        {/* {openModal && } */}
        <Form onSubmit={handlePay}>
            <Row>
                {alertMessage && <Alert>{alertMessage}</Alert>}
                <Col md={6}>
                    <Form.Group className='md-3'>
                        <Form.Label>First Name</Form.Label>
                        <Form.Control type='text' placeholder='FirstName' value={user.firstName} disabled />
                    </Form.Group>
                </Col>
                <Col md={6}>
                    <Form.Group className='md-3'>
                        <Form.Label>Last Name</Form.Label>
                        <Form.Control type='text' placeholder='FirstName' value={user.lastName} disabled />
                    </Form.Group>
                </Col>
                <Col md={6}>
                    <Form.Group className='md-3'>
                        <Form.Label>Email</Form.Label>
                        <Form.Control type='text' placeholder='Email' value={user.email} disabled />
                    </Form.Group>
                </Col>
            </Row>
            <Row>
                <Col md={7}>
                    <Form.Group className='md-3'>
                        <Form.Label>Address</Form.Label>
                        <Form.Control
                            type='text' 
                            placeholder='Address' 
                            value={address} onChange={(e) => setAddress(e.target.value)} 
                            required
                        />
                    </Form.Group>
                </Col>
                <Col md={5}>
                    <Form.Group className='md-3'>
                        <Form.Label>Country</Form.Label>
                        <Form.Control 
                            type='text' 
                            placeholder='Country' 
                            value={country} onChange={(e) => setCountry(e.target.value)}
                            required
                        />
                    </Form.Group>
                </Col>
            </Row>
            <label htmlFor='card-element'>Card</label>
            <CardElement id="card-element"/>
            <Button 
                className='mt-3' 
                type='submit' 
                disabled={user.cart.count <= 0 || paying || isSuccess}>
                {paying ? "Processing..." : "Pay"}
            </Button>
        </Form>
    </Col>
  )
}

export default CheckoutForm